import React, { useState, useCallback, useEffect, useRef } from "react";
import cnchar from "cnchar";
import { Button, Input, Divider, Modal, message } from "antd";
import {
  ArrowRightOutlined,
  RedoOutlined,
  BulbOutlined,
  FrownOutlined,
  QuestionOutlined,
  SettingOutlined,
  HeartTwoTone,
  MenuOutlined,
} from "@ant-design/icons";

import {
  useGameData,
  RETRY_TIMES,
  getSpell,
  updateIdiomSet,
  getCount,
} from "../utils/gameData";

import Data, { LIST_END_ID } from "./Data";

import "./Game.css";

let hintIndex = 0;

const Game = () => {
  const data = useGameData();
  const inputRef = useRef();
  const [input, setInput] = useState("");
  const [spell, setSpell] = useState("");
  const [drawer, setDrawer] = useState(false);
  const [about, setAbout] = useState(false);
  const [settingVisible, setSettingVisible] = useState(false);
  const [allMode, setAllMode] = useState(false);

  const scrollToEnd = useCallback(() => {
    setTimeout(() => {
      const end = document.getElementById(LIST_END_ID);
      end && end.scrollIntoView(false);
    }, 50);
  }, []);

  const hint = useCallback(() => {
    message.info(
      <>
        <b>{data.answer[hintIndex]}</b>
      </>
    );
    setDrawer(false);
  }, [data]);

  const random = useCallback(
    (slient) => {
      hintIndex = Math.floor(Math.random() * 10) % 4;
      setInput("");
      data.init();
      !slient && message.success("");
      setDrawer(false);
    },
    [data]
  );

  const restart = useCallback(() => {
    data.restart();
    setDrawer(false);
    message.success("");
  }, [data]);

  const submitInput = useCallback(() => {
    if (!input) {
      return;
    }
    if (!data.validate(input)) {
      message.error("");
      return;
    }
    try {
      data.match(input);
      setInput("");
      scrollToEnd();
      inputRef.current.blur();
    } catch (e) {
      console.error(e);
      message.error(e.message);
    }
  }, [input, data, scrollToEnd]);

  const stop = useCallback(() => {
    setDrawer(false);
    setTimeout(() => {
      data.stop();
      scrollToEnd();
    }, 50);
  }, [data, scrollToEnd]);

  const switchSet = useCallback(() => {
    const v = !allMode;
    updateIdiomSet(v);
    setAllMode(v);
    random(true);
  }, [random, allMode]);

  const openAbout = useCallback(() => {
    setAbout(true);
    setDrawer(false);
  }, []);

  const openSetting = useCallback(() => {
    setSettingVisible(true);
    setDrawer(false);
  }, []);

  useEffect(() => {
    let nextVal = "";
    const text = input
      .split("")
      .filter((el) => cnchar.isCnChar(el))
      .join("");
    nextVal = getSpell(text).join(" ");
    if (nextVal !== spell) {
      setSpell(nextVal);
    }
  }, [input, data, spell]);

  return (
    <div className="game">
      <div className="game-pane">
        <Button onClick={random} shape="circle" icon={<ArrowRightOutlined />} />
        <Button onClick={data.restart} shape="circle" icon={<RedoOutlined />} />
        <Button
          onClick={() => setDrawer(true)}
          shape="circle"
          icon={<MenuOutlined />}
        />
      </div>

      <div className="game-container">
        <h4 className="ans">
          {data.isOver ? (
            <>
              
              <a
                href={`https://www.bing.com/search?q=+${data.answer}`}
                target="_blank"
                rel="noreferrer"
              >
                {data.answer}
              </a>
            </>
          ) : (
            `${RETRY_TIMES - data.history.length}`
          )}
        </h4>

        <Data data={data} />

        <div className="spell">{spell || " "}</div>
        <div>
          <Input.Search
            ref={inputRef}
            className="game-input"
            value={input}
            placeholder=""
            enterButton={data.isOver ? "" : ""}
            onChange={(e) => setInput(e.target.value)}
            onSearch={submitInput}
          />
        </div>
      </div>

      <Modal
        visible={about}
        title=""
        footer={null}
        centered
        onCancel={() => setAbout(false)}
      >
        <div className="about-container">
          <b></b>
          <div className="rule">
            <span className="rule-tag" style={{ color: "green" }}>
              
            </span>
            : 
            <br />
            <span className="rule-tag" style={{ color: "orange" }}>
              
            </span>
            : 
            <br />
            <span className="rule-tag" style={{ color: "lightgray" }}>
              
            </span>
            : 
          </div>
          <Divider />
          <div>
            <a
              className="link"
              href="https://handle.antfu.me/"
              target="_blank"
              rel="noreferrer"
            >
              
            </a>
            &emsp;/&emsp;
            <a
              className="link source"
              href="https://github.com/LzxHahaha/handle-infinite"
              target="_blank"
              rel="noreferrer"
            >
              {"   ( ) "}
            </a>
          </div>
          <Divider />
          <div></div>
          <Button
            onClick={() =>
              message.success("")
            }
            shape="circle"
            icon={<HeartTwoTone twoToneColor="#eb2f96" />}
          />
        </div>
      </Modal>

      <Modal
        visible={settingVisible}
        title=""
        footer={null}
        onCancel={() => setSettingVisible(false)}
      >
        <div className="data-set">
          
          {allMode ? "" : ""} ({getCount()})&emsp;
          <Button shape="round" size="small" onClick={switchSet}>
            
          </Button>
        </div>
      </Modal>

      <Modal
        maskClosable
        closable={false}
        visible={drawer}
        footer={null}
        bodyStyle={{ bottom: 0 }}
        onCancel={() => setDrawer(false)}
      >
        <div className="drawer-menu">
          <Button onClick={random} icon={<ArrowRightOutlined />} block>
            
          </Button>
          <Button onClick={restart} icon={<RedoOutlined />} block>
            
          </Button>
          <Button onClick={hint} icon={<BulbOutlined />} block>
            
          </Button>
          <Button onClick={stop} danger icon={<FrownOutlined />} block>
            
          </Button>
          <Divider />
          <Button
            onClick={openSetting}
            icon={<SettingOutlined />}
            block
          >
            
          </Button>
          <Button
            onClick={openAbout}
            icon={<QuestionOutlined />}
            block
          >
            
          </Button>
        </div>
      </Modal>
    </div>
  );
};

export default Game;
